<template>
	<div>
		<nav class="navbar navbar-transparent navbar-absolute">
			<div class="container">
				<div class="navbar-header">
					<button type="button" class="navbar-toggle"
							data-toggle="collapse"
							data-target="#navigation-example-2"
							@click="toggleNavbar">
						<span class="sr-only">Переключить навигацию</span>
						<span class="icon-bar"></span>
						<span class="icon-bar"></span>
						<span class="icon-bar"></span>
					</button>
					<span class="navbar-brand">Reagent Store</span>
				</div>
				<div class="collapse navbar-collapse">
					<ul class="nav navbar-nav navbar-right">
						<router-link to="/login" tag="li"><a>Вход</a></router-link>
					</ul>
				</div>
			</div>
		</nav>
		<div class="wrapper wrapper-full-page">
			<div class="full-page login-page" data-color="blue" data-image="static/img/background/background.jpg">
				<!--   you can change the color of the filter page using: data-color="blue | azure | green | orange | red | purple" -->
				<div class="content">
					<div class="container">
						<div class="row">
							<div class="col-md-4 col-sm-6 col-md-offset-4 col-sm-offset-3">
								<form method="#" action="#">
									<div class="card" data-background="color" data-color="blue">
										<div class="card-header">
											<h3 class="card-title">Регистрация</h3>
										</div>
										<div class="card-content">
											<div class="form-group">
												<label>Ваше имя</label>
												<input type="text" name="userName" v-validate="modelValidations.userName" v-model="model.userName"
													placeholder="Введите ваше имя" class="form-control input-no-border" data-vv-as="ваше имя">
												<small class="text-danger" v-show="userName.invalid">{{ errors.first('userName') }}</small>
											</div>
											<div class="form-group">
												<label>Эл.почта</label>
												<input type="email" name="email" v-validate="modelValidations.email" v-model="model.email"
													placeholder="Введите Эл.почту" class="form-control input-no-border" data-vv-as="Эл.почта">
												<small class="text-danger" v-show="email.invalid">{{ errors.first('email') }}</small>
											</div>
											<div class="form-group">
												<label>Пароль</label>
												<input type="password" name="password" v-validate="modelValidations.password" v-model="model.password"
													placeholder="Пароль" class="form-control input-no-border" data-vv-as="Пароль">
												<small class="text-danger" v-show="password.invalid">{{ errors.first('password') }}</small>
											</div>
											<div class="form-group">
												<label>Повторите пароль</label>
												<input type="password" name="confirmPassword" v-validate="modelValidations.confirmPassword" v-model="model.verifyPassword"
													placeholder="Повторите пароль" class="form-control input-no-border" data-vv-as="подтверждения пароля">
												<small class="text-danger" v-show="confirmPassword.invalid">{{ errors.first('confirmPassword') }}</small>
											</div>
										</div>
										<div class="card-footer text-center">
											<button type="submit" class="btn btn-fill btn-wd" @click.prevent="validate" v-bind:disabled="processRegistration">Продолжить</button>
											<div class="forgot m-t-10"><router-link to="/login">Уже есть аккаунт?</router-link></div>
										</div>
									</div>
								</form>
							</div>
						</div>
					</div>
				</div>
				<footer class="footer footer-transparent">
					<div class="container"><div class="copyright">&copy; 2019, NSK</div></div>
				</footer>
				<div class="full-page-background" style="background-image: url(static/img/background/background.jpg) "></div>
			</div>
		</div>
		<div class="collapse navbar-collapse off-canvas-sidebar">
			<ul class="nav nav-mobile-menu">
				<router-link to="/login" tag="li"><a>Вход</a></router-link>
			</ul>
		</div>
	</div>
</template>
<script>
	import { mapFields } from 'vee-validate'
	import swal from 'sweetalert2'
	export default {
		computed: {
			...mapFields(['userName', 'email', 'password', 'confirmPassword'])
		},
		props: {
			referral: {
				default: null
			}
		},
		data () {
			return {
				model: {
					userName: '',
					email: '',
					password: '',
					verifyPassword: ''
				},
				modelValidations: {
					userName: {
						required: true,
						min: 3
					},
					email: {
						required: true,
						email: true
					},
					password: {
						required: true,
						min: 6
					},
					confirmPassword: {
						required: true,
						min: 6
						// confirmed: 'password'
					}
				},
				processRegistration: false
			}
		},
		methods: {
			toggleNavbar () {
				document.body.classList.toggle('nav-open')
			},
			closeMenu () {
				document.body.classList.remove('nav-open')
				document.body.classList.remove('off-canvas-sidebar')
			},
			validate () {
				this.$validator.validateAll().then(isValid => {
					if (isValid) {
						this.register()
					}
				})
			},
			register () {
				if (this.model.password !== this.model.verifyPassword) {
					return
				}
				var self = this
				swal({
					title: 'Регистрация',
					text: 'Регистрация пользователя в системе...',
					onOpen: () => {
						swal.showLoading()
					},
					showConfirmButton: false,
					allowOutsideClick: false,
					allowEscapeKey: false,
					allowEnterKey: false
				})
				self.processRegistration = true
				if (this.referralCode !== null) {
					this.model.referral = this.referral
				}
				this.$store.dispatch('userSignup', {
					account: this.model,
					callback: function () {
						swal({
							title: 'Успех',
							text: 'Подтвердите ваш адрес электронной почты...',
							type: 'success',
							showConfirmButton: true
						})
					},
					successFalse: function (data) {
						swal({
							title: 'Упс',
							text: 'Что то пошло не так...',
							type: 'warning',
							showConfirmButton: true
						})
						if (data.messages && Object.values(data.messages).length) {
							self.errors.clear()
							for (var field in data.messages) {
								self.errors.add(field, data.messages[field])
								self[field].invalid = !(self[field].valid = false)
							}
						}
						swal.close()
						self.processRegistration = false
					}
				})
			}
		},
		beforeDestroy () {
			this.closeMenu()
		}
	}
</script>
<style>
</style>



